import React, { Component } from 'react';


//on va partir d'un composant enrobé pour en sortir
// un autre composant
const handleClicks = (WrappedComponent) => {

    //On créer un composant de type state
    class HandleClicks extends Component {

        state = {
            bg: ''
        }

        handleClick = () => {

            if(WrappedComponent.name === "Gingembrebout") {
                this.setState({
                    bg:'bg-danger'
                })
            } else {
                this.setState({
                    bg:'bg-success'
                })
            }
        }

        render() {

            if(this.state.bg === "bg-danger") {
                
                throw new Error();
            }

            return<WrappedComponent clickHandler = {this.handleClick} 
            backGround = {this.state.bg}  />
        }

        
    }

    return HandleClicks    
} 
export default handleClicks;