import React, { Component } from 'react';
import curcuma from '../curcuma.jpg';
import handleClicks from './HOC';


class Curcuma extends Component {
    /*
    Commenter ou supprimer pour les mettre uniquement dans le
    composant d'ordre supérieur, afin d'éviter la doublure et 
    rendre le code plus performant
       state = {
        bg:''
    }

    handleClick = (e) => {
        this.setState({
            bg:'bg-success'
        })
    }  
    */

    render () {

        const {backGround, clickHandler} = this.props;

        return (
            /* remplacer this.state.bg par backGround et 
            this.handleClick/clickHandler */
            <div className = {`col ${backGround}`}>
                <img onClick = {clickHandler} src = {curcuma} 
                alt= "curcuma" /><br />
                <p>Curcuma du Benin</p>
            </div>
        )
    }
}

export default handleClicks(Curcuma) ;