import React, { Component } from 'react';

class RGPD extends Component {

    onLaw = () => {
        alert('Nous appliquons la loi sur Règlement Général sur la Protection des Données de nos utilisateurs, cliquez sur ok pour continuer');
    }
    
    render() {

        return (

            <div>
                <p onLaw = {this.onLaw ()}></p>
            </div>
        
        ) 
           
    }    
}
export default RGPD;