import React, { Component } from 'react';
import gingembrebouteille from '../gingembrebouteille.jpg';
import handleClicks from './HOC';

class Gingembrebout extends Component {

    /*
    Commenter ou supprimer pour les mettre uniquement dans le
    composant d'ordre supérieur, afin d'éviter la doublure et 
    rendre le code plus performant
    state = {
        bg:''
    }

    handleClick = () => {
        this.setState({
            bg: 'bg-danger'
        })
    }
    */


     render () {

        const {backGround, clickHandler} = this.props;
       
        /*
        if(this.state.bg === "bg-danger") {
            throw new Error();
        }
        */
        

        return (
            /* remplacer this.state.bg par backGround et 
            this.handleClick/clickHandler */
            <div className={`col ${backGround}`}>
                <img onClick={clickHandler} src={gingembrebouteille} 
                alt="gingembrebouteille" /> <br />
                <p>Boisson fraiche de gingembre</p>
            </div>
        )
    }
}
export default handleClicks(Gingembrebout);